    line-height: 14px;
  }
}
.bonuses {
  background: #ffffff;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.1);
  margin: 0 0 20px 0;
  display: flex;
  flex-direction: column;
}
.bonuses__text {
  font-size: 15px;
  line-height: 30px;
  letter-spacing: 0.01em;
  color: #151a18;
  margin: 0 0 20px 0;
}
.bonuses__title {
  margin: 0 0 10px 0;
}
.bonuses__list {
  width: 100%;
  margin: 0 0 30px 0;
}
.bonuses__item {
  display: grid;
  grid-template-columns: 1fr 320px 120px 100px 100px;
  align-items: center;
}
.bonuses__item:not(:last-child) {
  margin: 0 0 20px 0;
}
.bonuses__item-title {
  display: flex;
  align-items: center;
}
.bonuses__item-title img {
  margin: 0 14px 0 0;
}
.bonuses__item-title-txt {
  font-size: 15px;
  line-height: 22px;
  color: #151a18;
}
.bonuses__item-offer {
  font-size: 15px;
  line-height: 22px;
  color: #151a18;
}
.bonuses__item-deposit {
  font-size: 15px;
  line-height: 22px;
  color: #151a18;
}
.bonuses__item-code {
  font-size: 15px;
  line-height: 22px;
  color: #151a18;
}
.bonuses__item-link {
  display: inline-block;
  padding: 12px 29px 10px;
  font-size: 15px;
  line-height: 20px;
}
.bonuses__item-th {
  display: grid;
  grid-template-columns: 1fr 320px 120px 100px 100px;
  align-items: center;
  font-size: 15px;
  line-height: 22px;
  color: #858585;
  text-align: start;
  margin: 0 0 15px 0;
  padding: 0 0 15px 0;
  border-bottom: 1px solid #f3f5f6;
}
.bonuses__all-link {
  align-self: center;
  display: inline-block;
  padding: 14px 30px 12px;
  font-size: 15px;
  line-height: 20px;
}

@media screen and (max-width: 1285px) {
  .bonuses__item-th {
    display: none;
  }
  .bonuses__list {
    display: grid;
    grid-template-columns: auto auto;
    gap: 20px;
  }
  .bonuses__item {
    grid-template-columns: auto auto;
    grid-template-rows: auto auto auto auto;
    border-radius: 4px;
    background: #f3f5f6;
    padding: 20px;
  }
  .bonuses__item:not(:last-child) {
    margin: 0;
  }
  .bonuses__item-title {
    grid-row: 1/2;
    grid-column: 1/3;
    margin: 0 0 15px 0;
  }
  .bonuses__item-offer {
    grid-row: 2/3;
    grid-column: 1/3;
    font-family: "Popins-semb", sans-serif;
    margin: 0 0 20px 0;
  }
  .bonuses__item-deposit {
    grid-row: 3/4;
    grid-column: 1/2;
  }
  .bonuses__item-code {
    grid-row: 4/5;
    grid-column: 1/2;
  }
  .bonuses__item-button {
    grid-row: 3/5;
    grid-column: 2/3;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}
@media screen and (max-width: 1024px) {
  .bonuses {
    padding: 30px 0 20px;
    display: flex;
    border-radius: 0;
    flex-direction: column;
    margin: 0 0 20px 0;
  }
  .bonuses__all-link {
    align-self: center;
    width: 80%;
    text-align: center;
    font-size: 15px;
    line-height: 20px;
  }
  .bonuses__title {
    width: 80%;
    margin: 0 auto 10px auto;
  }
  .bonuses__text {
    width: 80%;
    margin: 0 auto 20px auto;
  }
  .bonuses__list {
    display: flex;
    flex-direction: column;
    margin: 0 auto 20px auto;
    width: 80%;
  }
}
@media screen and (max-width: 600px) {
  .bonuses {
    padding: 30px 15px 20px;
  }
  .bonuses__title {
    width: 100%;
  }
  .bonuses__text {
    width: 100%;
    font-size: 13px;
    line-height: 25px;
  }
  .bonuses__list {
    width: 100%;
  }
  .bonuses__all-link {
    width: 100%;
    text-align: center;
  }
}
.quote {
  padding: 30px;
  background: linear-gradient(180deg, #085a3d 0%, #038941 100%);
  border-radius: 8px;
  position: relative;
  margin: 0 0 20px 0;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.1);
}
.quote::before {
  pointer-events: none;
  content: "‟";
  font-size: 65px;
  line-height: 65px;
  transform: scale(1, -1);
  height: 18px;
  color: #e7fd52;
  position: absolute;
  display: inline-block;
  top: 45px;
  left: 30px;
}
.quote::after {
  pointer-events: none;
  content: "‟";
  font-size: 65px;
  line-height: 65px;
  transform: scale(-1, 1);
  height: 18px;
  color: #e7fd52;
  position: absolute;
  display: inline-block;
  top: 55px;
  left: 52px;
}
.quote__text {
  font-size: 15px;
  line-height: 30px;
  letter-spacing: 0.01em;
  font-style: italic;
  color: #ffffff;
}
.quote__info {
  display: flex;
  flex-direction: column;
}
.quote__info-wrap {
  margin: 0 0 20px 0;
  display: flex;
  align-items: center;
  padding: 0 0 0 60px;
}
.quote__info-image {
  width: 60px;
  height: 60px;
  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  border: 1px solid #e7fd52;
  border-radius: 4px;
  margin: 0 12px 0 0;
}
.quote__info-user-name {
  font-size: 15px;
  line-height: 20px;
  color: #ffffff;
}
.quote__info-user-date {
  font-size: 11px;
  line-height: 20px;
  color: #ffffff;
}

@media screen and (max-width: 1024px) {
  .quote {
    border-radius: 0;
    margin: 0 0 20px 0;
  }
}
@media screen and (max-width: 600px) {
  .quote {
    padding: 30px 15px;
  }
  .quote::before {
    right: 48px;
    left: auto;
  }
  .quote::after {
    right: 25px;
    left: auto;
  }
  .quote__info-wrap {
    margin: 0 0 20px 0;
    padding: 0 60px 0 0;
  }
  .quote__text {
    font-size: 13px;
    line-height: 28px;
  }
}